@tailwind base;
@tailwind components;
@tailwind utilities;

//
/* CSS VARIABLE ROOT */
:root {
	--radius-sm: 0.125rem;
	--radius-DEFAULT: 0.25rem;
	--radius-md: 0.375rem;
	--radius-lg: 0.5rem;
	--radius-xl: 0.75rem;
	--radius-2xl: 1rem;
	--radius-3xl: 1.5rem;
	--radius-full: 9999px;

	/* SCREENS */
	--media-md: 768px;
	--media-lg: 1024px;
}

//
.woostify-blocks-settings-page-php {
	#wpwrap {
		@apply bg-white;
	}
	#wpcontent {
		@apply pl-0;
	}
}
